let zhChoose = 'zh_hk'; // 

// ,
let zhS = '';
let zhT = '';

String.prototype.tran = function () {
	let s1, s2;
	if (zhChoose === 'zh_hk') {
		s1 = zhS;
		s2 = zhT;
	} else if (zhChoose === 'zh_cn') {
		s1 = zhT;
		s2 = zhS;
	} else {
		return this;
	}
    let a = '';
	let l = this.length;
	for (let i = 0; i < this.length; i++) {
		let c = this.charAt(i);
		let p = s1.indexOf(c);
		a += p < 0 ? c : s2.charAt(p);
    }
	return a;
};

function zhTranBody (obj) {
	let o = (typeof (obj) === 'object') ? obj.childNodes : document.body.childNodes;
	for (let i = 0; i < o.length; i++) {
		let c = o.item(i);
		if ('||BR|HR|TEXTAREA|SCRIPT|'.indexOf('|' + c.tagName + '|') > 0) continue;
		if (c.title !== '' && c.title != null) {
			c.title = c.title.tran();
		}
		if (c.alt !== '' && c.alt != null) {
			c.alt = c.alt.tran();
		}
		if (c.tagName === 'INPUT' && c.value !== '' && c.type !== 'text' && c.type !== 'hidden' && c.type !== 'password') {
			c.value = c.value.tran();
		}
		if (c.nodeType === 3) {
			c.data = c.data.tran();
		} else {
			zhTranBody(c);
		}
	}
}

let changeLang = {
    zhTran: (go) => {
        zhChoose = go;
        zhTranBody();
    }
};

export default changeLang;



